"use client";

import { useState, useEffect } from "react";
import { useAuth } from "./useAuth";

// Progress data from database
export interface UserProgress {
  id: string;
  userId: string;
  courseId: string;
  plantId: string;
  progressPercent: number;
  currentSection?: string;
  lastActiveAt: string;
  createdAt: string;
  updatedAt: string;
}

// Enrollment data from database
export interface UserEnrollment {
  id: string;
  userId: string;
  courseId: string;
  plantId: string;
  status: "enrolled" | "in_progress" | "completed";
  enrolledAt: string;
  completedAt?: string;
  createdAt: string;
  updatedAt: string;
}

// Combined user course status
export interface UserCourseStatus {
  courseId: string;
  enrollment?: UserEnrollment;
  progress?: UserProgress;
  status: "not_started" | "enrolled" | "in_progress" | "completed";
  progressPercent: number;
}

export function useUserProgress() {
  const { user, profile } = useAuth();
  const [progress, setProgress] = useState<UserProgress[]>([]);
  const [enrollments, setEnrollments] = useState<UserEnrollment[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchUserProgress = async () => {
      if (!user) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);

        // Fetch user progress and enrollments in parallel
        const [progressResponse, enrollmentsResponse] = await Promise.all([
          fetch('/api/progress'),
          fetch('/api/enrollments')
        ]);

        if (!progressResponse.ok) {
          throw new Error(`Failed to fetch progress: ${progressResponse.statusText}`);
        }

        if (!enrollmentsResponse.ok) {
          throw new Error(`Failed to fetch enrollments: ${enrollmentsResponse.statusText}`);
        }

        const progressData = await progressResponse.json();
        const enrollmentsData = await enrollmentsResponse.json();

        if (!progressData.success) {
          throw new Error(progressData.error || 'Failed to fetch progress');
        }

        if (!enrollmentsData.success) {
          throw new Error(enrollmentsData.error || 'Failed to fetch enrollments');
        }

        // Filter data for current user only
        const userProgress = progressData.data.filter((p: UserProgress) => 
          p.userId === user.id
        );
        const userEnrollments = enrollmentsData.data.filter((e: UserEnrollment) => 
          e.userId === user.id
        );

        setProgress(userProgress);
        setEnrollments(userEnrollments);
      } catch (err) {
        console.error('Error fetching user progress:', err);
        setError(err instanceof Error ? err.message : 'Failed to fetch user progress');
      } finally {
        setLoading(false);
      }
    };

    fetchUserProgress();
  }, [user]);

  // Helper function to get course status
  const getCourseStatus = (courseId: string): UserCourseStatus => {
    const enrollment = enrollments.find(e => e.courseId === courseId);
    const userProgress = progress.find(p => p.courseId === courseId);

    let status: UserCourseStatus['status'] = 'not_started';
    let progressPercent = 0;

    if (enrollment) {
      if (enrollment.status === 'completed') {
        status = 'completed';
        progressPercent = 100;
      } else if (enrollment.status === 'in_progress') {
        status = 'in_progress';
        progressPercent = userProgress?.progressPercent || 0;
      } else {
        status = 'enrolled';
        progressPercent = userProgress?.progressPercent || 0;
      }
    }

    return {
      courseId,
      enrollment,
      progress: userProgress,
      status,
      progressPercent,
    };
  };

  return {
    progress,
    enrollments,
    loading,
    error,
    getCourseStatus,
    refetch: () => {
      if (user) {
        setLoading(true);
        setError(null);
      }
    }
  };
}
